@font-face {
  font-family: 'Playfair Display';
  src: url("./fonts/PlayfairDisplay-Bold.ttf") format("truetype");
  font-weight: 700;
  font-style: normal; }

@font-face {
  font-family: 'Playfair Display';
  src: url("./fonts/PlayfairDisplay-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'Lato';
  src: url("./fonts/Lato-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'Lato';
  src: url("./fonts/Lato-Light.ttf") format("truetype");
  font-weight: 300;
  font-style: normal; }

h1 {
  font-size: 36px; }

p {
  font-size: 22px; }

a {
  color: #e24c9c;
  font-weight: 700;
  font-family: "Playfair Display"; }

.hashtag {
  font-family: "Lato"; }

html, body {
  box-sizing: border-box;
  color: #222222;
  font-family: "Playfair Display";
  font-size: 100% !important;
  height: 100%; }

* {
  box-sizing: inherit; }

.app {
  padding: 32px;
  max-width: 1024px;
  margin: 0 auto;
  text-align: center; }

.the-comic img {
  display: block;
  margin: 0 auto;
  max-width: 600px; }

.app-header {
  max-width: 550px;
  margin: 0 auto; }
  .app-header h1 {
    margin: 0 0 16px; }
  .app-header p {
    display: block;
    margin: 0 auto;
    max-width: 70%; }
  .app-header .description .hashtag {
    font-weight: 700; }

.app-footer p {
  font-size: 16px; }

.app-footer ul {
  list-style-type: none;
  padding: 0; }
  .app-footer ul li {
    display: inline-block;
    margin: 0 4px; }
  .app-footer ul a {
    color: #e24c9c; }

.app-footer .copynicht {
  font-size: 12px; }
